}






























































































































































































































































































































































































































































































































































































/* 說明 */
.lineChart .detail {
    color: gray;
}
.lineChart .chartContain {
    max-width: 600px;
    margin: 0 auto;
    position: relative;
}
.lineChart .chartContain #calPath {
      display: none;
}
.lineChart .chartContain .tooltip {
      min-width: 100px;
      background-color: rgba(0, 0, 0, 0.9);
      color: white;
      padding: 10px;
      border-radius: 6px;
      position: absolute;
      text-align: left;
      line-height: 1.5em;
      z-index: 2
}
.lineChart .chartContain .tooltip.hidden {
    visibility: hidden;
}



























































































































































































































































































































































































































































